:root {
  --font-family: proxima-nova, sans-serif;

  --color-dark: #222;
  --color-aoa-blue: #006;
  --color-sea-serpent: #11c7eb;
  --color-teal: #22a49c;
  --color-gold: #fa0;
  --color-warm: #f9f7f5;

  --blueStartColor: #8cbfed;
  --blueStopColor: #6785a1;
  --yellowStartColor: #fffecc;
  --yellowStopColor: #fffc66;
  --seaStartColor: #27e5f7;
  --seaStopColor: #11c7eb;
  --whiteStartColor: #f9f7f5;
  --whiteStopColor: #f9f7f5;

  --primary-color: var(--color-aoa-blue);

  --font-color-dark: var(--color-dark);

  --bezier-timing: 0.175, 0.885, 0.32, 1.275;
}

html {
  font-size: 100%;
  line-height: 1;
}

body {
  font-family: var(--font-family);
  font-size: 16px;
}

html::after,
body::before {
  content: none;
}

img {
  max-width: 100%;
  height: auto;
}

.wrap {
  max-width: 1140px;
  height: 100%;
  margin: 0 auto;
}

.etc {
  height: 800px;
  width: 100%;
  position: relative;
  margin-top: 9rem;
  background-color: rebeccapurple;
}

.etc:before {
  content: 'Etc.';
  position: absolute;
  padding: 1rem;
  font-family: var(--font-family);
  font-size: 24px;
}

.blobs {
  height: 100%;
  width: 100%;
  position: absolute;
  top: -10%;
}

.blob {
  position: absolute;
  height: auto;
}

.blob svg {
  /*width: 90vmin;*/
  /*height: 90vmin;*/
}

.blob--blue {
  width: 1200px;
  top: 189px;
  left: 677px;

  z-index: 10; /* TODO temp */
}

.blob--yellow {
  width: 1061px;
  top: 175px;
  left: 1039px;
  mix-blend-mode: lighten;
}

.blob--white {
  width: 1069px;
  top: 170px;
  left: -200px;
}

.blob--sea {
  width: 1525px;
  top: 258px;
  left: -220px;
}

.super {
  position: relative;
  overflow: hidden;
}

.super-fig {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

.super-fig.loaded:after {
  content: '';
  position: absolute;
  left: -32px;
  right: -32px;
  top: 20%;
  bottom: 0;
  z-index: 1;
  background-image: linear-gradient(
    to top,
    #fff 30%,
    rgba(255, 255, 255, 0.02)
  );
}

.super-fig__body {
  position: absolute;
  z-index: 2;
  bottom: 180px;
}

.super-fig__kicker {
  font-size: 12px;
  letter-spacing: 3px;
  text-transform: uppercase;
  text-align: center;
  color: var(--color-sea-serpent);
}

.super-fig__title {
  margin-top: 27px;
  margin-bottom: 24px;
  font-size: 44px;
  line-height: 1;
  text-align: center;
  font-weight: 800;
  color: var(--primary-color);
}

.super-fig__blurb {
  max-width: 654px;
  margin: 0 auto;
  font-size: 18px;
  letter-spacing: 0;
  line-height: 1.55;
  color: var(--font-color-dark);
}

.super-fig__link {
  position: relative;
  padding-right: 24px;
  font-weight: 600;
  text-decoration: none;
  color: var(--color-teal);
}

.super-fig__link:after {
  content: url(data:image/svg+xml;base64,);
  position: absolute;
  top: 0;
  margin-left: 7px;
  transition: transform 0.2s ease-out;
}

.super-fig__link:hover:after {
  transform: translate3d(3px, 0, 0);
}

.super-fig__fig {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  max-width: 1140px;
  height: 100%;
  margin: 0 auto;
}

.super-fig__bg {
  position: absolute;
  top: 240px;
  left: 20px;
  right: -10px;
  bottom: -40px;
  z-index: -1;
  background-image: linear-gradient(to bottom, var(--color-sea-serpent), #fff);
}

.super-fig__image {
  /*max-width: 1204px;*/
  height: auto;
}
